import React from "react";
import ReactDOM from "react-dom";
import { BrowserRouter, Route, Switch, Redirect } from "react-router-dom";
import { createBrowserHistory } from "history";
import Async from "react-code-splitting";

import "./assets/vendor/nucleo/css/nucleo.css";
import "./assets/vendor/font-awesome/css/font-awesome.min.css";
import "./assets/scss/argon-design-system-react.scss";

const LandingPage = () => (
  <Async load={import("./views/Landing/LandingPage.jsx")} />
);
const WebPortfolio = () => (
  <Async load={import("./views/Landing/WebPortfolio.jsx")} />
);
const DesignPortfolio = () => (
  <Async load={import("./views/Landing/DesignPortfolio.jsx")} />
);
const Projects = () => <Async load={import("./views/examples/Projects.jsx")} />;

const hist = createBrowserHistory();

ReactDOM.render(
  <BrowserRouter history={hist}>
    <Switch>
      <Route path="/" exact render={props => <LandingPage {...props} />} />
      <Route
        path="/web-portfolio"
        exact
        render={props => <WebPortfolio {...props} />}
      />
      <Route
        path="/design-portfolio"
        exact
        render={props => <DesignPortfolio {...props} />}
      />
      <Route path="/projects" exact render={props => <Projects {...props} />} />
      <Redirect to="/" />
    </Switch>
  </BrowserRouter>,
  document.getElementById("root")
);
